import {
	carouselList,
	myrequest,
	recommendurl,
} from '../../api.js';
export default {
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: []
		};
	},

	onLoad() {
		this.loadData();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			this.titleNViewBackground = carouselList[0].background;
			this.swiperLength = carouselList.length;
			this.carouselList = carouselList;
			let goodsList = await myrequest(recommendurl, 'GET', {});
			this.goodsList = goodsList["data"] || [];
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		navToDetailPage(item) {
			let id = item.id;
			uni.navigateTo({
				url: `/pages/service/product?id=${id}`
			})
		},
		tabClick(index) {
			if(index === 4){
				uni.navigateTo({
					url: '../collection/collection'
				})
			}else{
				uni.setStorage({
					key: 'serviceIndex',
					data: index,
				});
				uni.switchTab({
					url: `/pages/service/service`
				})
			}
		},
	},
}
